<template>
<div class="home" >
  <LMap :style="mapScreen" :bounds.sync="bounds" :center.sync="center" :zoom.sync="zoom" :max-zoom="18" :options="mapOptions">

    <LTileLayer
      v-for="tileProvider in tileProviders"
      :key="tileProvider.name"
      :name="tileProvider.name"
      :visible="tileProvider.visible"
      :url="tileProvider.url"
      :attribution="tileProvider.attribution"
      layer-type="base"
    />

    <LLayerGroup layer-type="overlay" name="Projects 222">
      <l-marker
        v-for="marker in points"
        :lat-lng="[marker.lat, marker.lng]"
        :key="marker.lat"
      >
        <l-icon
          :icon-size="[40, 40]"
          :icon-anchor="[22, 94]"
          icon-url="http://icons.iconarchive.com/icons/paomedia/small-n-flat/1024/map-marker-icon.png"
        />
      </l-marker>
    </LLayerGroup>
    <LLayerGroup layer-type="overlay" name="Projects" ref="projs" title="Hello">
      <LGeoJson :geojson="geojson" :options="options">
        <l-icon
          :icon-size="[40, 40]"
          :icon-anchor="[22, 94]"
          icon-url="http://icons.iconarchive.com/icons/paomedia/small-n-flat/1024/map-marker-icon.png"
        />
      </LGeoJson>
    </LLayerGroup>

    <LControl class="example-custom-control">
      <PopMenu @selected="menuSelected"/>
    </LControl>

    <LControlLayers />
  </LMap>
  <Dashboard v-if="showChart" :style="chartScreen"/>
  </div>
</template>

<script>
// @ is an alias to /src
import store from "../store";
import PopMenu from "../components/PopMenu.vue";
import basemaps from "@/data/basemaps.json";
import Dashboard from '@/views/Dashboard.vue'

import {
  LMap,
  LIcon,
  LMarker,
  LControlLayers,
  LPopup,
  LPolyline,
  LPolygon,
  LRectangle,
  LTileLayer,
  LLayerGroup,
  LControl,
  LGeoJson,
} from "vue2-leaflet";
import { latLngBounds, latLng, CRS } from "leaflet";

const points = [
  { lat: 10.79, lng: 102.14 },
  { lat: 14.525, lng: 107.0343 },
];
const bounds = latLngBounds(points);
export default {
  name: "Home",
  props:{
    windowHeight: Number,
    windowWidth: Number
  },

  components: {
    LMap,
    LIcon,
    LMarker,
    LControlLayers,
    LPopup,
    LPolyline,
    LPolygon,
    LRectangle,
    LTileLayer,
    LLayerGroup,
    LControl,
    LGeoJson,
    PopMenu,
    Dashboard,
  },

  data() {
    const zoom = 8;
    const center = [11.5, 104.9];
    const geojson = store.getters.allProjects;
    return {
      fillColor: "#e4ce7f",
      //marker: latLng(11.5, 104.9),
      zoom,
      center,
      bounds: bounds,
      mapOptions: {
        zoomControl: true,
        attributionControl: true,
        zoomSnap: true,
      },
      tileProviders: basemaps,
      geojson: geojson,
      points: points,
      showChart: false
    };
  },
  computed: {
    options() {
      const fillColor = this.fillColor;
      return {
        onEachFeature: this.onEachFeatureFunction,
        style: function (feature) {
          if (feature.properties.lic_type === "IESIA") {
            return {
              weight: 2,
              color: "#00FF00",
              opacity: 0.5,
              fillColor: fillColor,
              fillOpacity: 0.2,
            };
          } else {
            return {
              weight: 2,
              color: "#FF0000",
            };
          }
        },
      };
    },
    onEachFeatureFunction() {
      return (feature, layer) => {
        layer.bindPopup(
          "<div>code:" +
            feature.properties.id +
            "</div><div>owner_kh: " +
            feature.properties.owner_kh +
            "</div>",
          { permanent: false, sticky: true }
        );
      };
    },

    mapScreen() {
      return this.showChart? this.isLandscape()? "width: 50%; float:right" : "height: 50%; float:top"
              : '';
    },
    
    chartScreen() {
      return this.isLandscape()? "width: 50%; float:left" : "height: 50%; float:bottom";
    }
  },
  methods: {
    isLandscape() {
      return this.windowWidth > this.windowHeight;
    },
    menuSelected(index){
      if(index === 0){
        this.showChart = !this.showChart;
      }
    }
  },
  watch: {
    zoom() {
      // console.log(this.windowWidth);
    },
  },
};
</script>


<style scoped>
.home {
  width: 100%;
  height: 100%;
}

.example-custom-control {
  background: #fff;
  padding: 0 0.5em;
  border: 1px solid #aaa;
  border-radius: 0.1em;
}
.custom-control-watermark {
  font-size: 200%;
  font-weight: bolder;
  color: #aaa;
  text-shadow: #555;
}
</style>
