<template>
  <div class="chart-display">
    <chart-selector class="chart-selector" @selectChanged="chartSelected" />
    <ChartAbstract v-if="selectedChart==0"/>
    <ChartByYear v-if="selectedChart==1"/>
    <ChartByProvince v-if="selectedChart==2"/>
    <ChartByType v-if="selectedChart==3"/>
    <ChartByCategory v-if="selectedChart==4"/>
  </div>
</template>

<script>
import ChartSelector from '@/components/ChartSelector.vue';
import ChartAbstract from '@/components/ChartAbstract.vue';

export default {
    name: 'Dashboard',
    components:{
        ChartSelector,
        ChartAbstract,
        ChartByYear: () => import('../components/ChartByYear.vue'),
        ChartByCategory: () => import('../components/ChartByCategory.vue'),
        ChartByProvince: () => import('../components/ChartByProvince.vue'),
        ChartByType: () => import('../components/ChartByType.vue'),
    },
    data(){
        return{
            selectedChart: 0
        }
    },
    methods:{
        chartSelected(index){
            this.selectedChart = index;
        }
    }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Moul&family=Nokora:wght@100;400&display=swap');

.chart-selector{
    margin: 20px;
}
.highcharts-title{
    font-family: 'Moul', cursive;
    font-size: 14px;
}
.highcharts-axis-title{
    font-family: 'Nokora', sans-serif;
    font-size: 14px;
}
.highcharts-legend-item {
    font-family: 'Nokora', sans-serif;
}

</style>