<template>
  <div @click="toggleShowMenu = !toggleShowMenu">
    <ul  :class="'main-menu ' + (toggleShowMenu? 'button-close':'') ">
      <li>
        <a href="#">
          <i :class="mainMenuClass" aria-hidden="true" />
          {{ toggleShowMenu ? "Cancal" : "" }}
        </a>
      </li>
    </ul>

    <div v-if="toggleShowMenu" @click="toggleShowMenu = !toggleShowMenu">
      <ul class="menu">

        <li>
          <a href="#" @click="onClick(0)">
            <i class="fa fa-chart-line" aria-hidden="true" />
            ក្រាហ្វិក
          </a>
        </li>

        <li>
          <a href="#" @click="onClick(1)">
            <i class="fa fa-list" aria-hidden="true" />
            បញ្ជីគម្រោង
          </a>
        </li>

        <li>
          <a href="#" @click="onClick(2)">
            <i class="fa fa-search" aria-hidden="true" />
            Search
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import router from "@/router";
export default {
  name: "PopMenu",

  data() {
    return {
      toggleShowMenu: false,
    };
  },
  methods: {
    navToDashboard(item) {
      router.push("/dashboard");
    },
    onClick(index){
      this.toggleShowMenu = !this.toggleShowMenu;
      this.$emit('selected', index);
    }
  },
  computed:{
    mainMenuClass(){
      return this.toggleShowMenu? 'fa fa-times button-close':'fa fa-bars';
    }
  }
};
</script>

<style scoped>
.button-close {
    color: red;
    padding: 0 5px 0 0;
}
.button-close > li > a {
  color: red;
}
ul {
  list-style: none;
}
.context-menu {
  position: relative;
}
.main-menu {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgb(64 64 64 / 5%);
  padding: 0px 12px;
}
.main-menu > li > a {
  font-family: 'Nokora', sans-serif;
  border: 0;
  padding: 0;
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  text-decoration: unset;
  
}
.menu {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgb(64 64 64 / 5%);
  padding: 10px 0;
}
.menu > li > a {
  font-family: 'Nokora', sans-serif;
  font-size: 14px;
  border: 0;
  padding: 10px 30px 10px 15px;
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  text-decoration: unset;
  color: #000;
  font-weight: 500;
  transition: 0.5s linear;
  -webkit-transition: 0.5s linear;
  -moz-transition: 0.5s linear;
  -ms-transition: 0.5s linear;
  -o-transition: 0.5s linear;
}
.menu > li > a:hover {
  background: #f1f3f7;
  color: black;
}
.menu > li > a > i {
  padding-right: 10px;
}
.menu > li.trash > a:hover {
  color: red;
}
</style>
