import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    projects: []
  },
  mutations: {
    initProjectList(state, projects) {
      state.projects = projects;
    }
  },

  getters: {
    allProjects: (state, getters) => {
      return state.projects;
    }
  },

  actions: {
  },
  modules: {
  }
})
